* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-size: 10px;
}

body {
  background: rgb(244,244,244);
  color: #333;
}

p {
  font-size: 1.6rem;
}

.app {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 4.4rem;
  text-align: center;
}

.form {
  display: grid;
}

.label {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}

.input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
  outline: none;
}

.button {
  background: rgba(0,0,0,0.75);
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0,0,0,0.75);
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background .25s;
  outline: none;
}

.button:hover {
  background: rgba(0,0,0,0.85);
}

/* Styling for movies*/
.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
  margin-bottom: 2rem;
  background: #fff;
}

.card--list {
  margin-top: 4rem;
}

.card--title {
  margin: 0.5rem 0;
  font-size: 3.2rem;
  text-align: center;
}

.card--image {
  margin: 0 auto;
  display: block;
}

/* Styling for Footer */
footer {
  background: transparent;
  color: #000;
  text-align: center;
  padding: 3em 0 0 0;
}

footer p {
  font-size: 12px;
}

#love {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

#love a {
  color: rgb(9, 64, 216);
  text-decoration: none;
}

#love > span {
  font-family: "Old Standard TT", serif;
  font-weight: 400;
  font-size: 12px;
  color: #000;
}

#love > img {
  width: 30px;
  padding: 2px 6px 4px;
}

@media (min-width: 600px){
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }

  .input {
    margin-bottom: 0;
  }

  .card--list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .card {
    flex: 45%;
    margin: 10px;
  }
}

